.button {
  background-color: #fff;
  border: 0;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  padding: 7px 14px;
  font-size: 8px;
  transition-duration: .25s;
  display: flex;
}

.button__span--arrow {
  padding-right: 15px;
}

.button__red {
  color: #fff;
  background-color: #d41342;
}

.button__red:hover, .button__red:focus {
  background-color: #c7133e;
}

.button__red svg {
  fill: #fff;
}

.button__white {
  color: #d41342;
  background-color: #fff;
}

.button__white:hover, .button__white:focus {
  background-color: #ecebeb;
}

.button__white svg {
  fill: #d41342;
}

.button__white, .button__red {
  line-height: 10px;
}

.button__arrow {
  width: 40px;
  height: 40px;
  background: #fff url("button-arrow.5a357a40.png") center no-repeat;
}

.button__menu {
  width: 190px;
  padding: 12px 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 21px;
}

.button__section {
  width: 176px;
  padding: 12px 0;
  font-weight: 700;
  line-height: 18px;
}

.button__section--medium {
  font-size: 14px;
  line-height: 18px;
}

.button__section--big {
  font-size: 16px;
  line-height: 21px;
}

@media (min-width: 1200px) {
  .button__red--hero, .button__white--hero {
    padding: 12px 20px;
    font-size: 14px;
    line-height: 18px;
  }

  .button__menu {
    width: 160px;
  }

  .button__section--big {
    width: 200px;
  }
}

.hamburger {
  justify-content: flex-end;
  display: flex;
  position: relative;
}

.current {
  color: #d41443;
}

.menu-container {
  width: 100vw;
  height: 100vh;
  z-index: 999;
  background-color: #e17992;
  transition: transform .6s ease-out;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  transform: translateX(0%);
}

.mobile-menu {
  height: 100%;
  flex-direction: column;
  margin-top: 60px;
  display: flex;
}

.mobile-menu__button {
  justify-content: center;
  margin-bottom: 19px;
  display: flex;
}

.mobile-menu__link {
  color: #fff;
  margin-left: 21px;
  font-weight: 700;
  line-height: 1.28;
  text-decoration: none;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.mobile-menu__link:hover, .mobile-menu__link:focus {
  color: #d41443;
  outline: none;
}

.mobile-menu__border {
  border-top: 1px solid #e18298;
  padding: 13px 0;
}

.mobile-menu__border--bottom {
  border-bottom: 1px solid #e18298;
}

.menu-toggle {
  min-height: 40px;
  min-width: 40px;
  fill: #d41443;
  cursor: pointer;
  background-color: #0000;
  border: none;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

@media (min-width: 1200px) {
  .menu-toggle {
    display: none;
  }
}

.menu-toggle:hover, .menu-toggle:focus {
  fill: #c7133e;
}

.menu-container .menu-toggle {
  fill: #fff;
  position: absolute;
  top: 20px;
  right: 20px;
}

.menu-container .menu-toggle:hover, .menu-container .menu-toggle:focus {
  fill: #d41443;
}

body {
  color: #000;
  background-color: #fff;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

address {
  font-style: normal;
}

a {
  text-decoration: none;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

